.page-header {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  padding: 28px 0;
  background-color: transparent;
  z-index: 10;
}

.page-header > div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1414px;
  min-width: 960px;
  flex-basis: 95%;
}

.page-header__right {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  max-width: 80%;
  margin-top: -15px;
}

.page-header__right a,
.page-header__right button {
  margin-top: 15px;
}

.page-header__right button {
  margin-right: 15px;
}

.page-header__right a:last-child,
.page-header__right button:last-child {
  margin-right: 0;
}

.page-header__email,
.page-header__phone {
  font-family: "PT Mono", "Cambria", serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: $white;
  text-decoration: none;

  &:hover,
  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.page-header__email svg,
.page-header__phone svg {
  margin-top: 5px;
}

.page-header__email {
  margin-right: 89px;
}

.page-header__email span {
  margin-left: 9px;
}

.page-header__phone {
  margin-right: 46px;
}

.page-header__phone span {
  margin-left: 6px;
}

@media (max-width: $tablet-width) {
  .page-header {
    padding: 18px 3.2%;
  }

  .page-header > div {
    min-width: 717px;
  }

  .page-header__right {
    max-width: 500px;
  }

  .page-header__right a {
    margin-right: 33px;
  }

  .page-header__right a:last-of-type {
    margin-right: 0;
  }

  .page-header__email span,
  .page-header__phone span {
    display: none;
  }

  .page-header__email svg {
    width: 25.33px;
    height: 18.67px;
    margin-top: 0;
  }

  .page-header__phone svg {
    width: 24px;
    height: 24px;
    margin-top: 0;
  }
}

@media (max-width: $mobile-width) {
  .page-header {
    padding: 8px 5.3%;
  }

  .page-header > div {
    min-width: 287px;
  }

  .page-header__right a {
    margin-right: 22px;
  }

  .page-header__email {
    margin-right: 22px;
  }
}
